const Item = props => {
  const { classes } = props;
  return (
    <>
      <Grid item sm={6}>
        <Card className={classes.cardBackground}>
          <div className={classes.cardHeader}>
            <img className={classes.img} src={props.item.img} />
            <p className={classes.cardTitle}>{props.item.title}</p>
          </div>
          <div className={classes.content}>
            {topics[props.item.title.toLowerCase()].map(topic => (
              <a className={classes.link} href={topic.link}>
                {topic.title}
              </a>
            ))}
          </div>
        </Card>
      </Grid>
    </>
  );
};

